import React from 'react'
import { Menu, Avatar, Row, Col } from 'antd';
import { StockOutlined, ContactsOutlined } from '@ant-design/icons';
import {
  Link
} from "react-router-dom";

import Pic from '../static/images/avatar1.png'



class Navbar extends React.Component {
  state = {
    current: 'stock-app',
  };

  handleClick = e => {
    console.log('click ', e);
    this.setState({ current: e.key });
  };

  render() {
    const { current } = this.state;
    return (
      
        <Menu style = {{padding: "15px",  fontSize: "14px"}} onClick={this.handleClick} selectedKeys={[current]} mode="horizontal">
          <Row>
            <Col span = {4}>
                <Menu.Item key = "stock-app" icon={<StockOutlined style={{ fontSize: '150%'}} />}>
                  <Link to="/stock-app">
                      Stock App           
                  </Link>
                </Menu.Item>
            </Col>
            <Col span = {4}>
                <Menu.Item key="app" icon={<Avatar style={{ fontSize: '150%', paddingTop: "-50px" ,backgroundColor: 'white' }} src={Pic} />}>
                  <Link to="/Profile">
                      Profile           
                  </Link>
                </Menu.Item>
            </Col>
            <Col span = {4}>
                <Menu.Item key="Contact" icon={<ContactsOutlined style={{ fontSize: '150%' }} />}>
                  <Link to="/contact">
                      Contact           
                  </Link>
                </Menu.Item>
            </Col>
          </Row>
        </Menu>
      
    );
  }
}

export default Navbar