/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from 'react'
import { Typography,  Row, Col } from 'antd';
import {CaretUpOutlined, CaretDownOutlined} from '@ant-design/icons';
import '../static/css/ProcessData.css'
import DisplayAsset from './DisplayAsset';
import AssetChart from './AssetChart';
import { Scrollbars } from 'react-custom-scrollbars';
    



export default function DataFetch(props) {

    const {data} = props
    const { Text } = Typography;
    const [assetSymbol, setAssetSymbol] = useState("")
    const [assetName, setAssetName] = useState("")
    const [flag, setflag] = useState(true)
    const asset_data = data.map(asset => {
        
        return(
            {
                asset_symbol: asset.symbol,
                asset_name: asset.name,
                asset_image : asset.image,
                asset_price: asset.current_price,
                change_24h: asset.price_change_percentage_1h_in_currency ? asset.price_change_percentage_1h_in_currency.toFixed(2) : 0
            }
        )
    })

    async function handleClick(e){
        setAssetSymbol(e.target.id);
        setAssetName(e.target.innerHTML)
    }

    useEffect(() => {
        console.log("Assets re-rendered");
    }, [flag])

    function handleAscendingSort() {
        console.log("called");
        asset_data.sort(function(a, b){return a.change_24h - b.change_24h})
        console.log(asset_data);
        setflag(!flag)
    }

    return (
        
        <div>
            {!asset_data && <h2>Processing...</h2>} 
            <div className="assets_wrapper">
                
                <Row>
                    <Col span = {6}>
                        <div className = "assets-heading">
                            <Row>
                                <Col span = {24}>
                                    <Row>
                                        <Col offset={2} span = {6}>
                                            <Text type="secondary">Coin Name</Text>
                                        </Col>
                                        <Col offset = {1} span = {5}>
                                            <Text type="secondary">Price(USD)</Text>
                                        </Col>
                                        <Col offset = {1} span = {6}>
                                            <Text type="secondary">Change(24h)  
                                            </Text>
                                        </Col>
                                        <Col>
                                            <Row>   
                                                <CaretUpOutlined onClick = {handleAscendingSort} />
                                            </Row>
                                            <Row>   
                                                <CaretDownOutlined />
                                            </Row>
                                        </Col>
                                    </Row>
                                </Col>
                                
                                <hr />
                            </Row>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Col span = {6}>
                        <Scrollbars style={{ width: 350, height: 600 }}>
                            {console.log(asset_data)}
                            {asset_data.map((asset, index) => {
                                return(
                                    <div className = "assets-grid">
                                        <Row>
                                            <Col span = {24} className = "assets-shadow">
                                                <DisplayAsset assetSymbol = {assetSymbol} index = {index} asset = {asset} handleClick = {handleClick} />  
                                            </Col>
                                            <hr />
                                        </Row>
                                    </div>
                                )
                            })}
                        </Scrollbars>
                    </Col>
                    <Col span = {16}>
                        {assetSymbol && <AssetChart assetSymbol = {assetSymbol} assetName = {assetName}/>}
                    </Col>
                </Row>
            </div>
        </div>
    )
}

